import { AfterViewInit, Component, ElementRef, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ViewportTriggerService } from '../commons/services/viewport-trigger.service';
import { CommonModule } from '@angular/common';

@Component({
  standalone: true,
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  imports: [CommonModule],
})
export class HomeComponent implements AfterViewInit {
  @ViewChild('animatable') sectionRef!: ElementRef;
  animationState = 'hidden';
  constructor(
    private http: HttpClient,
    private vt: ViewportTriggerService
  ) {}

    ngAfterViewInit() {
    this.vt.observe(this.sectionRef.nativeElement, {
      topThreshold: 0.3,
      onEnter: () => (this.animationState = 'visible'),
      onExit: () => (this.animationState = 'hidden')
    });
  }

  downloadCurriculum() {
    const url = 'assets/files/Braynner Polo Curriculum vitae - English.pdf'; // reemplaza con la ruta del archivo que deseas descargar
    this.http.get(url, { responseType: 'blob' }).subscribe((response: Blob) => {
      const urlObject = URL.createObjectURL(response);
      const a = document.createElement('a');
      a.href = urlObject;
      a.download = 'Braynner Polo Curriculum vitae.pdf'; // reemplaza con el nombre que deseas dar al archivo descargado
      a.click();
      URL.revokeObjectURL(urlObject);
    });
  }
}
