import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ViewportTriggerService {

  observe(
    element: Element,
    {
      topThreshold = 0.3,
      onEnter,
      onExit,
    }: {
      topThreshold?: number;
      onEnter: () => void;
      onExit?: () => void;
    }
  ) {
    const observer = new IntersectionObserver(
      ([entry]) => {
        const topRatio = entry.intersectionRatio > 0;
        if (topRatio && entry.isIntersecting) {
          onEnter(); // Call this when the element enters the view
        } else {
          onExit?.(); // Call this when the element leaves the view (if defined)
        }
      },
      {
        root: null, // 'null' means we observe with respect to the viewport
        rootMargin: `0px 0px -${(1 - topThreshold) * 100}% 0px`,
        threshold: topThreshold
      }
    );

    observer.observe(element);
    return observer;
  }
}
